import React, { useState, useRef, useEffect } from 'react';
import CustomNode from './CustomNode';
import UserNode from './UserNode';
import {
  ReactFlow,
  MiniMap,
  Background,
  Controls,
  useReactFlow
} from '@xyflow/react';
import '@xyflow/react/dist/base.css';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import DownloadButton from './DownloadButton';

const nodeTypes = {
  customNode: CustomNode,
  userNode: UserNode,
};

// Custom renderer for bold text to add background color
const markdownComponents = {
  strong: ({node, ...props}) => (
    <strong className="p-1 rounded bg-yellow-700/60">{props.children}</strong>
  ),
};

const FlowDiagram = ({ data, roleMembers, onGraphPermissionNodeClick, graphPermissionApps }) => {
  // Move filter state here
  const [filter, setFilter] = useState('');

  const matchedNodes = data.nodes.filter(node =>
    node.data.label.toLowerCase().includes(filter.toLowerCase())
  );
  const matchedNodeIds = new Set(matchedNodes.map(node => node.id));

  const visited = new Set();

  // Collect all relevant node ids recursively
  const relevantNodeIds = new Set();
  const relevantEdgeIds = new Set();

  const addNodeAndLinked = (nodeId) => {
    if (visited.has(nodeId)) return;
    visited.add(nodeId);
    relevantNodeIds.add(nodeId);

    // First level: include both incoming and outgoing edges
    const directlyConnected = new Set();

    data.edges.forEach(edge => {
      if (edge.source === nodeId) {
        directlyConnected.add(edge.target);
        relevantNodeIds.add(edge.target);
        relevantEdgeIds.add(edge.id || `${edge.source}-${edge.target}`);
      }
      if (edge.target === nodeId) {
        directlyConnected.add(edge.source);
        relevantNodeIds.add(edge.source);
        relevantEdgeIds.add(edge.id || `${edge.source}-${edge.target}`);
      }
    });

    // Second level: follow only outgoing paths recursively
    directlyConnected.forEach(targetId => {
      followOutgoing(targetId);
    });
  };

  const followOutgoing = (nodeId) => {
    if (visited.has(nodeId)) return;
    visited.add(nodeId);
    relevantNodeIds.add(nodeId);

    data.edges.forEach(edge => {
      if (edge.source === nodeId) {
        relevantNodeIds.add(edge.target);
        relevantEdgeIds.add(edge.id || `${edge.source}-${edge.target}`);
        followOutgoing(edge.target);
      }
    });
  };

  // Start from each matched node
  matchedNodeIds.forEach(addNodeAndLinked);

  const filteredNodes = data.nodes.filter(node => relevantNodeIds.has(node.id));
  const filteredEdges = data.edges.filter(edge =>
    relevantNodeIds.has(edge.source) &&
    relevantNodeIds.has(edge.target) &&
    relevantEdgeIds.has(edge.id || `${edge.source}-${edge.target}`)
  );

  // Popup state for node info
  const [selectedNode, setSelectedNode] = useState(null);
  const [markdown, setMarkdown] = useState('');
  const popupRef = useRef(null);

  // Close popup when clicking outside
  useEffect(() => {
    function handleClickOutside(event) {
      if (
        selectedNode &&
        popupRef.current &&
        !popupRef.current.contains(event.target)
      ) {
        setSelectedNode(null);
      }
    }
    if (selectedNode) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [selectedNode]);

  // Clear markdown when popup is closed
  useEffect(() => {
    if (!selectedNode) {
      setMarkdown('');
    }
  }, [selectedNode]);

  // Handle node click
  const onNodeClick = async (_event, node) => {
    setSelectedNode(node);
    // For all nodes, try to load markdown
    try {
      const md = await import(`../nodes/${node.id}.md`);
      const response = await fetch(md.default);
      const text = await response.text();
      setMarkdown(text);
    } catch (e) {
      setMarkdown('No information available for this node.');
    }
  };

  return (
    <div
      style={{
        height: '100vh',
        borderTop: '1px solid #ddd',
        // borderRadius: '15px',
        overflow: 'hidden',
        color: 'white'
      }}
      className="relative"
    >
      {/* Filter input in top right corner */}
      <div className="absolute top-6 right-4 z-20">
        <input
          type="text"
          placeholder="Type to filter..."
          value={filter}
          onChange={e => setFilter(e.target.value)}
          className="p-2 w-60 border border-gray-700 bg-gray-800 text-gray-100 rounded shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
      </div>
      {/* Smaller legend below filter input */}
      <div className="absolute right-4 top-16 mt-2 z-20 bg-gray-900 bg-opacity-80 rounded px-2 py-1 border border-gray-700 flex items-center gap-1 text-xs text-gray-300 shadow">
        <span
          className="inline-block w-5 h-1 rounded"
          style={{ background: '#16a34a' }}
        ></span>
        Green lines are direct privilege escalations to Global Admin
      </div>
      <ReactFlow
        nodes={filteredNodes.map(node =>
          node.id === 'user'
            ? { ...node, type: 'userNode' }
            : node
        )}
        edges={filteredEdges}
        nodeTypes={nodeTypes}
        colorMode={'dark'}
        minZoom={0.01}
        fitView
        onNodeClick={onNodeClick}
      >
        {/* <MiniMap /> */}
        <Background />
        <Controls />
        <DownloadButton />
      </ReactFlow>

      {/* Popup */}
      {selectedNode && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-60 overflow-y-auto backdrop-blur-sm">
          <div
            ref={popupRef}
            className="bg-gray-900 rounded-lg shadow-lg max-w-4xl w-full p-6 relative my-8 border border-white"
            style={{
              maxHeight: '90vh',
              overflowY: 'auto'
            }}
          >
            <button
              className="absolute top-2 right-2 text-gray-400 hover:text-white text-2xl"
              onClick={() => setSelectedNode(null)}
              aria-label="Close"
            >
              &times;
            </button>
            <div className="prose prose-invert prose-a:text-white prose-headings:text-white max-w-none text-gray-100">
              <ReactMarkdown
                remarkPlugins={[remarkGfm]}
                components={markdownComponents}
              >
                {markdown}
              </ReactMarkdown>
              {/* Show Entra role members if this is an Entra role node and members are available */}
              {selectedNode.data?.isEntraRole && roleMembers && roleMembers[selectedNode.id] && (
                <div className="mt-6">
                  <div className="font-bold text-lg mb-2 text-white">Members of this Entra role:</div>
                  <ul className="list-disc ml-6">
                    {roleMembers[selectedNode.id].length === 0 && (
                      <li className="text-gray-400">No members found.</li>
                    )}
                    {roleMembers[selectedNode.id].map(member => (
                      <li key={member.id} className="mb-1">
                        <span className="font-mono text-sm text-blue-200">{member.displayName}</span>
                        {member.userPrincipalName && (
                          <span className="ml-2 text-xs text-gray-400">{member.userPrincipalName}</span>
                        )}
                        {member.mail && (
                          <span className="ml-2 text-xs text-gray-400">{member.mail}</span>
                        )}
                        {member.type && (
                          <span className="ml-2 text-xs text-gray-500">{member.type.replace('microsoft.graph.', '')}</span>
                        )}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              {/* Show Graph permission applications if this is a Graph permission node */}
              {selectedNode.data?.isGraphPermission && graphPermissionApps && (
                <div className="mt-6">
                  <div className="font-bold text-lg mb-2 text-white">Applications with this Graph API permission:</div>
                  <ul className="list-disc ml-6">
                    {(graphPermissionApps[selectedNode.id]?.length === 0 || !graphPermissionApps[selectedNode.id]) && (
                      <li className="text-gray-400">No applications found. Run the assessment first.</li>
                    )}
                    {(graphPermissionApps[selectedNode.id] || []).map(app => (
                      <li key={app.id} className="mb-1">
                        <span className="font-mono text-sm text-blue-200">{app.displayName}</span>
                        <span className="ml-2 text-xs text-gray-400">App ID: {app.appId}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FlowDiagram;
