import React from 'react';
import { Panel, useReactFlow, getNodesBounds, getViewportForBounds } from '@xyflow/react';
import { toPng } from 'html-to-image';
import { useToast } from './ToastContext';

function downloadImage(dataUrl) {
  const a = document.createElement('a');
 
  a.setAttribute('download', 'reactflow.png');
  a.setAttribute('href', dataUrl);
  a.click();
}
 
const imageWidth = 6144;
const imageHeight = 4608;
 
function DownloadButton() {
  const { getNodes } = useReactFlow();
  const { showToast, closeToast } = useToast();

  const onClick = () => {
    // Show toast: generating image
    showToast('Generating image...', 'info');
    setTimeout(closeToast, 5000);

    const nodesBounds = getNodesBounds(getNodes());
    const viewport = getViewportForBounds(nodesBounds, imageWidth, imageHeight, 0.3, 1.2);

    toPng(document.querySelector('.react-flow__viewport'), {
      backgroundColor: '#141414',
      width: imageWidth,
      height: imageHeight,
      style: {
        width: imageWidth,
        height: imageHeight,
        transform: `translate(${viewport.x}px, ${viewport.y}px) scale(${viewport.zoom})`,
      },
    }).then(dataUrl => {
      // Show toast: done (green)
      showToast('Image generated!', 'success');
      setTimeout(closeToast, 5000);
      downloadImage(dataUrl);
    });
  };

  return (
    <Panel position="bottom-left" style={{ left: 40, bottom: 0 }}>
      <button
        className="flex items-center gap-2 px-3 py-1.5 rounded-md hover:bg-white/10 border border-gray-300 shadow-sm text-xs text-white-700 font-medium transition-all duration-150"
        style={{
          boxShadow: '0 2px 8px 0 rgba(0,0,0,0.07)',
          backdropFilter: 'blur(2px)',
        }}
        onClick={onClick}
        aria-label="Download as image"
      >
        {/* Export icon */}
        <svg
          className="w-4 h-4 text-blue-600"
          fill="none"
          stroke="currentColor"
          strokeWidth={2}
          viewBox="0 0 24 24"
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M12 16V4m0 12l-4-4m4 4l4-4M4 20h16" />
        </svg>
        <span>Export as image</span>
      </button>
    </Panel>
  );
}
 
export default DownloadButton;