import React from 'react';
import { Handle, Position } from '@xyflow/react';

export default function UserNode() {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', minWidth: 80 }}>
      <Handle type="target" position={Position.Left} id="left" style={{ background: '#555' }} />
      <div>
        <svg width={100} height={100} viewBox="0 0 48 48" fill="none">
          <circle cx="24" cy="18" r="10" fill="#afd2fc" />
          <ellipse cx="24" cy="36" rx="14" ry="8" fill="#60a5fa" />
        </svg>
      </div>
      <div style={{ color: '#fff', fontWeight: 600, fontSize: 18, marginTop: 2, fontFamily: "'Fira Mono', monospace" }}>
        User
      </div>
      <Handle type="source" position={Position.Right} id="right" style={{ background: '#555' }} />
    </div>
  );
}
