import React, { createContext, useContext, useState, useCallback } from 'react';

const ToastContext = createContext();

export function ToastProvider({ children }) {
  const [toast, setToast] = useState({
    open: false,
    message: '',
    progressRoles: null,
    progressGraph: null,
    status: '', // 'info' | 'success' | 'error'
  });

  const showToast = useCallback((message, status = 'info', progressRoles = null, progressGraph = null) => {
    setToast({ open: true, message, status, progressRoles, progressGraph });
  }, []);

  const updateToastProgressRoles = useCallback((current, total) => {
    setToast(t => ({ ...t, progressRoles: { current, total } }));
  }, []);

  const updateToastProgressGraph = useCallback((current, total) => {
    setToast(t => ({ ...t, progressGraph: { current, total } }));
  }, []);

  const closeToast = useCallback(() => setToast(t => ({ ...t, open: false })), []);

  return (
    <ToastContext.Provider value={{ showToast, closeToast, updateToastProgressRoles, updateToastProgressGraph }}>
      {children}
      <Toast toast={toast} closeToast={closeToast} />
    </ToastContext.Provider>
  );
}

export function useToast() {
  return useContext(ToastContext);
}

function Toast({ toast, closeToast }) {
  if (!toast.open) return null;
  return (
    <div className="fixed bottom-6 right-6 z-[100]">
      <div
        className={`flex flex-col gap-2 px-6 py-4 rounded-lg shadow-lg border
          ${toast.status === 'success'
            ? 'bg-green-700/90 border-green-400'
            : toast.status === 'error'
            ? 'bg-red-800/90 border-red-400'
            : 'bg-gray-800/90 border-blue-400'}
          min-w-[300px] max-w-xs animate-fade-in`}
        style={{ transition: 'all 0.3s' }}
      >
        <div className="flex items-center gap-2">
          {toast.status === 'success' && (
            <svg className="h-6 w-6 text-green-200" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
            </svg>
          )}
          {toast.status === 'error' && (
            <svg className="h-6 w-6 text-red-200" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
            </svg>
          )}
          {toast.status === 'info' && (
            <svg className="h-6 w-6 text-blue-200" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
              <circle cx="12" cy="12" r="10" />
              <path strokeLinecap="round" strokeLinejoin="round" d="M12 8v4m0 4h.01" />
            </svg>
          )}
          <span className="font-semibold text-white">{toast.message}</span>
        </div>
        {/* Entra roles progress bar */}
        {toast.progressRoles && (
          <div className="w-full mt-1">
            <div className="flex justify-between text-xs text-gray-200 mb-1">
              <span>Entra roles</span>
              <span>
                {toast.progressRoles.current} / {toast.progressRoles.total}
              </span>
            </div>
            <div className="w-full bg-gray-700 rounded h-2 overflow-hidden">
              <div
                className={`h-2 rounded transition-all duration-300 ${
                  toast.status === 'error'
                    ? 'bg-red-400'
                    : toast.status === 'success'
                    ? 'bg-green-400'
                    : 'bg-blue-400'
                }`}
                style={{
                  width: toast.progressRoles.total
                    ? `${Math.round(
                        (toast.progressRoles.current / toast.progressRoles.total) * 100
                      )}%`
                    : '0%',
                }}
              ></div>
            </div>
          </div>
        )}
        {/* Graph API permissions progress bar */}
        {toast.progressGraph && (
          <div className="w-full mt-1">
            <div className="flex justify-between text-xs text-gray-200 mb-1">
              <span>Graph API permissions</span>
              <span>
                {toast.progressGraph.current} / {toast.progressGraph.total || '?'}
              </span>
            </div>
            <div className="w-full bg-gray-700 rounded h-2 overflow-hidden">
              <div
                className={`h-2 rounded transition-all duration-300 ${
                  toast.status === 'error'
                    ? 'bg-red-400'
                    : toast.status === 'success'
                    ? 'bg-green-400'
                    : 'bg-blue-400'
                }`}
                style={{
                  width: toast.progressGraph.total
                    ? `${Math.round(
                        (toast.progressGraph.current / toast.progressGraph.total) * 100
                      )}%`
                    : '0%',
                }}
              ></div>
            </div>
          </div>
        )}
        <button
          className="absolute top-1 right-2 text-gray-300 hover:text-white text-xl"
          style={{ background: 'none', border: 'none' }}
          onClick={closeToast}
          aria-label="Close"
          type="button"
        >
          &times;
        </button>
      </div>
    </div>
  );
}
