import React, { useState, useEffect } from 'react';
import { useMsal } from '@azure/msal-react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

// --- MitigationsPopup component with RMAU logic ---
function MitigationsPopup({
  mitigationsPopupRef,
  mitigationsTab,
  setMitigationsTab,
  setMitigationsOpen,
  roleMembers,
  graphPermissionApps,
  initialData,
  mitigationsMarkdown,
  groupRMAUCache,
  setGroupRMAUCache,
  msalConfigReady
}) {
  // Always call hooks at the top level, unconditionally
  const msal = useMsal();
  const instance = msal.instance;
  const accounts = msal.accounts;
  const [rmauStatus, setRmauStatus] = useState({}); // { groupId: true/false }
  const [loadingGroups, setLoadingGroups] = useState({}); // { groupId: true }

  // --- Tooltip overlay state for RMAU ---
  const [hoveredRMAUGroupId, setHoveredRMAUGroupId] = useState(null);

  // Helper to fetch and cache RMAU status for a group
  const getRMAUStatus = async (groupId) => {
    if (!msalConfigReady || !instance || !accounts[0]) return false;
    if (rmauStatus[groupId] !== undefined) return rmauStatus[groupId];
    setLoadingGroups(prev => ({ ...prev, [groupId]: true }));
    try {
      const response = await instance.acquireTokenSilent({ account: accounts[0] });
      const token = response.accessToken;
      const res = await fetch(`https://graph.microsoft.com/beta/groups/${groupId}?$select=isManagementRestricted,displayName`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      if (!res.ok) throw new Error();
      const group = await res.json();
      setRmauStatus(prev => ({ ...prev, [groupId]: !!group.isManagementRestricted }));
      setGroupRMAUCache(prev => ({ ...prev, [groupId]: !!group.isManagementRestricted }));
      setLoadingGroups(prev => ({ ...prev, [groupId]: false }));
      return !!group.isManagementRestricted;
    } catch {
      setRmauStatus(prev => ({ ...prev, [groupId]: false }));
      setLoadingGroups(prev => ({ ...prev, [groupId]: false }));
      return false;
    }
  };

  // --- Automatically check RMAU status for all group members ---
  useEffect(() => {
    if (!msalConfigReady || !instance || !accounts[0]) return;
    // Collect all unique group IDs from roleMembers
    const groupIds = new Set();
    Object.values(roleMembers).forEach(members => {
      members.forEach(member => {
        const isGroup =
          (member.type && member.type.toLowerCase().includes('group')) ||
          (!member.type && member.id && member.id.length === 36);
        if (isGroup && member.id && rmauStatus[member.id] === undefined && groupRMAUCache[member.id] === undefined) {
          groupIds.add(member.id);
        }
      });
    });
    // Fetch RMAU status for each group
    groupIds.forEach(groupId => {
      getRMAUStatus(groupId);
    });
    // eslint-disable-next-line
  }, [roleMembers, msalConfigReady, instance, accounts]);

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-60 overflow-y-auto backdrop-blur-sm">
      {/* Overlay for RMAU tooltip */}
      {hoveredRMAUGroupId && (
        <div className="fixed inset-0 z-50 bg-black bg-opacity-40 backdrop-blur-sm transition-all duration-200 pointer-events-none"></div>
      )}
      <div
        ref={mitigationsPopupRef}
        className="bg-gray-900 rounded-lg shadow-lg max-w-5xl w-full p-6 relative my-8 border border-white"
        style={{
          maxHeight: '90vh',
          overflowY: 'auto',
        }}
      >
        <button
          className="absolute top-2 right-2 text-gray-400 hover:text-gray-700 text-2xl"
          onClick={() => setMitigationsOpen(false)}
          aria-label="Close"
          type="button"
        >
          &times;
        </button>
        {/* Tabs */}
        <div className="flex mb-6 border-b border-gray-700">
          <button
            className={`px-4 py-2 font-semibold text-sm rounded-t ${mitigationsTab === 0 ? 'bg-gray-800 text-white border-b-2 border-blue-400' : 'text-gray-400'}`}
            onClick={() => setMitigationsTab(0)}
            type="button"
          >
            Overview
          </button>
          <button
            className={`ml-2 px-4 py-2 font-semibold text-sm rounded-t ${mitigationsTab === 1 ? 'bg-gray-800 text-white border-b-2 border-blue-400' : 'text-gray-400'}`}
            onClick={() => setMitigationsTab(1)}
            type="button"
          >
            Mitigations
          </button>
        </div>
        {/* Tab content */}
        {mitigationsTab === 0 ? (
          Object.keys(roleMembers).length === 0 && Object.keys(graphPermissionApps).length === 0 ? (
            <div className="text-center text-gray-300 py-12">
              <div className="text-lg font-semibold mb-2">Assessment not run yet</div>
              <div className="mb-2">Please sign in and run the assessment first to view scan results of your Entra roles and Graph API permissions.</div>
              <div className="text-xs text-gray-500">Click the Sign in button and then "Start assessment" from the menu.</div>
            </div>
          ) : (
            <div className="overflow-y-auto max-h-[65vh]">
              {/* Entra Roles */}
              <div className="mb-8">
                <h3 className="text-lg font-bold mb-2 text-blue-200">Entra Roles</h3>
                <div className="space-y-8">
                  {Object.entries(roleMembers)
                    .filter(([_, members]) => Array.isArray(members) && members.length > 0)
                    .map(([nodeId, members]) => {
                      const node = initialData.nodes.find(n => n.id === nodeId);
                      if (!node) return null;
                      // --- Deduplicate users by id ---
                      const seenUserIds = new Set();
                      const dedupedMembers = [];
                      for (const member of members) {
                        // Only deduplicate users (not groups)
                        let isUser = false;
                        if (member.odataType) {
                          if (member.odataType === '#microsoft.graph.user') isUser = true;
                        } else if (member.type) {
                          const t = member.type.toLowerCase();
                          if (t.includes('user')) isUser = true;
                          else if (/pim (active|eligible)\s*\(user\)/i.test(member.type)) isUser = true;
                        } else if (member.userPrincipalName) {
                          isUser = true;
                        }
                        if (isUser) {
                          if (seenUserIds.has(member.id)) continue;
                          seenUserIds.add(member.id);
                        }
                        dedupedMembers.push(member);
                      }
                      return (
                        <div key={nodeId} className="rounded px-3">
                          <div className="font-semibold text-white mb-2">{node.data.label.replace(/\s*\(\d+\)$/, '')}</div>
                          <div className="overflow-x-auto">
                            <table className="min-w-full divide-y divide-gray-700 rounded-lg overflow-hidden border border-solid border-gray-800">
                              <thead>
                                <tr className="bg-gray-300">
                                  <th className="px-3 py-2 text-left text-xs font-semibold text-gray-800 uppercase">Display Name</th>
                                  <th className="px-3 py-2 text-left text-xs font-semibold text-gray-800 uppercase">Type</th>
                                  <th className="px-3 py-2 text-left text-xs font-semibold text-gray-800 uppercase">RMAU</th>
                                </tr>
                              </thead>
                              <tbody className="divide-y divide-gray-700 bg-gray-800">
                                {dedupedMembers.map(member => {
                                  // Use @odata.type for group/user detection if available
                                  let isGroup = false;
                                  let isUser = false;
                                  if (member.odataType) {
                                    if (member.odataType === '#microsoft.graph.group') isGroup = true;
                                    else if (member.odataType === '#microsoft.graph.user') isUser = true;
                                  } else if (member.type) {
                                    const t = member.type.toLowerCase();
                                    if (t.includes('group')) isGroup = true;
                                    else if (t.includes('user')) isUser = true;
                                    else if (/pim (active|eligible)\s*\(group\)/i.test(member.type)) isGroup = true;
                                    else if (/pim (active|eligible)\s*\(user\)/i.test(member.type)) isUser = true;
                                  } else {
                                    if (
                                      member.id &&
                                      /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i.test(member.id) &&
                                      !member.userPrincipalName
                                    ) {
                                      isGroup = true;
                                    } else if (member.userPrincipalName) {
                                      isUser = true;
                                    } else if (
                                      member.displayName &&
                                      /group$/i.test(member.displayName)
                                    ) {
                                      isGroup = true;
                                    }
                                  }
                                  let typeLabel = member.type
                                    ? member.type.replace('microsoft.graph.', '')
                                    : (member.odataType
                                        ? member.odataType.replace('#microsoft.graph.', '')
                                        : '');
                                  let badge = null;
                                  if (isGroup) {
                                    badge = <span className="ml-2 inline-block px-2 py-1 rounded bg-blue-900 text-blue-200 font-semibold text-xs">Group</span>;
                                  } else if (isUser) {
                                    badge = <span className="ml-2 inline-block px-2 py-1 rounded bg-blue-500 text-blue-200 font-semibold text-xs">User</span>;
                                  } else {
                                    badge = null;
                                  }
                                  // For users, show userPrincipalName as display name
                                  const displayName = isUser
                                    ? member.userPrincipalName || member.displayName || member.mail || ''
                                    : member.displayName;
                                  return (
                                    <tr key={member.id} className="hover:bg-gray-700/60 transition">
                                      <td className="px-3 py-2 font-mono text-blue-200 text-sm">{displayName}</td>
                                      <td className="px-3 py-2 text-xs text-gray-500">
                                        {typeLabel}
                                        {badge}
                                      </td>
                                      <td className="px-3 py-2 text-xs">
                                        {isGroup && member.id ? (
                                          <span
                                            className="relative group cursor-pointer"
                                            onMouseEnter={() => setHoveredRMAUGroupId(member.id)}
                                            onMouseLeave={() => setHoveredRMAUGroupId(null)}
                                            tabIndex={0}
                                            onFocus={() => setHoveredRMAUGroupId(member.id)}
                                            onBlur={() => setHoveredRMAUGroupId(null)}
                                          >
                                            {loadingGroups[member.id] ? (
                                              <span className="text-gray-400">Checking...</span>
                                            ) : (
                                              <span
                                                className={
                                                  groupRMAUCache[member.id] === true || rmauStatus[member.id] === true
                                                    ? "text-green-400 font-semibold"
                                                    : "text-red-400 font-semibold"
                                                }
                                              >
                                                {groupRMAUCache[member.id] === true || rmauStatus[member.id] === true
                                                  ? "RMAU"
                                                  : "Not RMAU"}
                                              </span>
                                            )}
                                            {/* Tooltip */}
                                            {hoveredRMAUGroupId === member.id && (
                                              <span className="absolute left-1/2 z-50 -translate-x-1/2 mt-2 w-80 px-3 py-2 rounded bg-gray-800 text-white text-xs shadow-lg opacity-100 pointer-events-auto border border-gray-700 transition-all duration-200">
                                                <b>RMAU</b> = Restricted Management Administrative Unit.<br />
                                                Groups marked as RMAU are protected from privileged role assignments by non-authorized admins.<br />
                                                <span className="text-blue-200">Why important?</span> Only RMAU groups can be used to securely delegate privileged roles (like Global Admin) to reduce the risk of privilege escalation by attackers or misconfigured admins.
                                              </span>
                                            )}
                                          </span>
                                        ) : (
                                          <span className="text-gray-500">/</span>
                                        )}
                                      </td>
                                    </tr>
                                  );
                                })}
                            </tbody>
                          </table>
                          </div>
                        </div>
                      );
                    })}
                </div>
              </div>
              {/* Graph API Permissions */}
              <div>
                <h3 className="text-lg font-bold mb-2 text-blue-200">Graph API Permissions</h3>
                <ul className="space-y-3">
                  {Object.entries(graphPermissionApps)
                    .filter(([_, apps]) => Array.isArray(apps) && apps.length > 0)
                    .map(([nodeId, apps]) => {
                      const node = initialData.nodes.find(n => n.id === nodeId);
                      if (!node) return null;
                      return (
                        <li key={nodeId} className="bg-gray-800 rounded p-3">
                          <div className="font-semibold text-white">{node.data.label.replace(/\s*\(\d+\)$/, '')}</div>
                          <ul className="ml-4 mt-1 text-sm text-gray-200">
                            {apps.map(app => (
                              <li key={app.id} className="mb-1">
                                <span className="font-mono">{app.displayName}</span>
                                <span className="ml-2 text-xs text-gray-400">App ID: {app.appId}</span>
                              </li>
                            ))}
                          </ul>
                        </li>
                      );
                    })}
                </ul>
              </div>
            </div>
          )
        ) : (
          <div className="prose prose-invert prose-a:text-white prose-headings:text-white max-w-none mb-4 text-white">
            <ReactMarkdown remarkPlugins={[remarkGfm]}>{mitigationsMarkdown}</ReactMarkdown>
          </div>
        )}
      </div>
    </div>
  );
}

export default MitigationsPopup;
