import React from 'react';
import { Handle, Position } from '@xyflow/react';

export default function CustomNode({ data }) {
  const isEntraRole = data.isEntraRole;
  const isGraphPermission = data.isGraphPermission;

  // Gradient border and shadow layer for Entra roles or Graph permissions
  const borderRadius = 16;
  const borderWidth = 3;

  const wrapperStyle =
    isEntraRole || isGraphPermission
      ? {
          position: 'relative',
          display: 'inline-block',
          borderRadius,
          padding: borderWidth,
          overflow: 'visible',
        }
      : {};

  const gradientBorderStyle = isEntraRole
    ? {
        position: 'absolute',
        zIndex: 0,
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        borderRadius,
        pointerEvents: 'none',
        background: 'linear-gradient(90deg, #e92a67 0%, #2a8af6 100%)',
        boxShadow:
          '0 0 8px #ffffff22, 0 0 12px #ae53ba44, 0 0 16px #2a8af644, 0 0 20px #e92a6733',
      }
    : isGraphPermission
    ? {
        position: 'absolute',
        zIndex: 0,
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        borderRadius,
        pointerEvents: 'none',
        background: 'linear-gradient(90deg, #2a8af6 0%, #60aaff 100%)',
        boxShadow:
          '0 0 8px #60aaff55, 0 0 12px #2a8af644, 0 0 16px #2a8af644, 0 0 20px #60aaff33',
      }
    : {};

  const nodeStyle = {
    fontFamily:
      "'Fira Mono', 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace",
    fontWeight: 500,
    fontSize: 17,
    letterSpacing: '-0.5px',
    lineHeight: 1.2,
    ...(isEntraRole
      ? {
          position: 'relative',
          zIndex: 1,
          padding: '16px 20px',
          borderRadius: borderRadius - borderWidth,
          minWidth: data.isGraphPermission ? 600 : 400,
          maxWidth: data.isGraphPermission ? 600 : 400,
          background: '#151515',
          color: '#fff',
          border: 'none',
          boxShadow:
            '0 1px 4px 0 #fff3, 0 0 4px 0 #ae53ba22, 0 0 6px 0 #2a8af611',
          textAlign: 'center',
        }
      : isGraphPermission
      ? {
          position: 'relative',
          zIndex: 1,
          padding: '16px 20px',
          borderRadius: borderRadius - borderWidth,
          minWidth: 600,
          maxWidth: 600,
          background: '#151d2b',
          color: '#fff',
          border: 'none',
          boxShadow:
            '0 1px 8px 0 #60aaff33, 0 0 8px 0 #2a8af622, 0 0 12px 0 #2a8af611',
          textAlign: 'center',
        }
      : {
          padding: 10,
          border: '1px solid #777',
          borderRadius: 5,
          minWidth: 350,
          maxWidth: 350,
          textAlign: 'center',
          background: 'none',
          color: 'white',
        }),
  };

  const subtitleStyle = isEntraRole
    ? {
        fontSize: 14,
        color: '#8b8b8b',
        fontFamily:
          "'Fira Mono', 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace",
        fontWeight: 400,
        marginTop: 4,
        letterSpacing: 0,
      }
    : {};

  return (
    <div style={wrapperStyle}>
      {(isEntraRole || isGraphPermission) && <div style={gradientBorderStyle} />}
      <div style={nodeStyle}>
        <Handle type="target" position={Position.Left} id="left" style={{ background: '#555' }} />
        <div>
          {data.label}
        </div>
        {isEntraRole && data.subtitle && (
          <div style={subtitleStyle}>{data.subtitle}</div>
        )}
        <Handle type="source" position={Position.Right} id="right" style={{ background: '#555' }} />
      </div>
    </div>
  );
}
